<template>
  <div id="app">
    <div id="viewContoller" :class="pageType">
      <router-view />
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      pageType: ""
    };
  },
  mounted() {
    var route = this.$route;
    if (
      route.name === "Signup" ||
      route.name === "Login" ||
      route.name === "ForgotPassword"
    ) {
      this.pageType = "public";
    } else {
      this.pageType = "restrict";
    }
  }
};
</script>

