import Vue from 'vue'
import VueRouter from 'vue-router'
import axios from 'axios'
import Login from '../views/public/Login.vue'

Vue.use(VueRouter)

function authRoute(to, from, next) {

  if (localStorage.getItem('token') !== null) {

    axios.defaults.headers.common = { 'Authorization': `bearer ` + localStorage.getItem("token") }

    axios.post('users/auth').then(resAuth => {

      if (resAuth.data.code == 200) {
        next();
      } else if (resAuth.data.code == 403) {
        localStorage.removeItem('token');
        next('/?erro=Acesso_negado_1');
      } else {
        localStorage.removeItem('token');
        next('/?erro=Acesso_negado_Erro_Resposta_Servidor');
      }
    }).catch(error => {

      if (error.response.status == 403) {
        next('/?erro='+error.response.data.error.message);
      }
    });

  } else {
    next('/?erro=token_inexistente');
    localStorage.removeItem('token');
  }
}

