import Vue from 'vue'
import ElementUI from 'element-ui';
import langElementUI from 'element-ui/lib/locale/lang/pt-br'
import localeElementUI from 'element-ui/lib/locale'
import 'element-ui/lib/theme-chalk/index.css';
import './assets/element-ui/theme/index.css';
import 'element-ui/lib/theme-chalk/display.css';
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import VueHead from 'vue-head'
import Gravatar from 'vue-gravatar';
import Axios from 'axios';


Vue.config.productionTip = false
localeElementUI.use(langElementUI);
Vue.use(ElementUI);
Vue.use(VueHead)
Vue.component('v-gravatar', Gravatar);


var str = window.location.origin;
if (str.match(/localhost/)) {
  Axios.defaults.baseURL = 'http://guiador.guiador.digital/api/'
} else {
  Axios.defaults.baseURL = Axios.defaults.baseURL = window.location.origin + '/api/'
}

Vue.use({
  install(Vue) {
    Vue.prototype.$http = Axios
  }
})


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
