<template>
  <div id="login">
    <el-row align="middle" justify="center">
      <el-col class="hidden-xs-only" id="bg-purple-waves" :sm="12" :md="12" :lg="12" :xl="12"></el-col>
      <el-col id="box-content" :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
        <div id="align-box">
          <header>
            <h1>GUIASALES</h1>
            <p>Bem vindo de volta. Você pode entrar em sua conta no formulário abaixo</p>
          </header>
          <main v-loading="this.form.loading">
            <el-form ref="form" :model="form" label-width="0px">
              <el-form-item>
                <el-input
                  placeholder="Digite seu e-mail..."
                  autofocus
                  @keyup.enter.native="submitLogin"
                  v-model="form.email"
                ></el-input>
              </el-form-item>
              <el-form-item>
                <el-input
                  placeholder="Digite sua senha..."
                  @keyup.enter.native="submitLogin"
                  v-model="form.password"
                  show-password
                ></el-input>
              </el-form-item>
              <el-form-item>
                <el-col :span="24" class="text-center">
                  <router-link to="/forgot-password">Esquecue sua senha?</router-link>
                </el-col>
              </el-form-item>
              <el-form-item class="text-center">
                <el-button type="primary" @click="submitLogin">Login</el-button>
                <el-button>
                  <router-link to="/sign-up">Cadastre-se</router-link>
                </el-button>
              </el-form-item>
            </el-form>
          </main>
          <footer></footer>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "Login",
  head: {
    title: {
      inner: "Login",
      complement: "GuiaSales"
    }
  },
  data() {
    return {
      form: {
        email: "",
        password: "",
        loading: false
      }
    };
  },
  methods: {
    submitLogin() {
      this.form.loading = true;

      if( this.checkFields() ){
        var dataForm = {
          email: this.form.email,
          password: this.form.password
      };
      this.$http
        .post("/signin", dataForm)
        .then(res => {
          this.form.loading = false;

          if (res.status == 200) {
            if( res.data.jwt != '' ){
              localStorage.setItem("token", res.data.jwt);
              this.$router.push("dashboard");
            }
          } else {
            this.$notify({
              title: "Erro",
              message: "Usuário/senha incorretos",
              type: "error",
              duration: 5000
            });
          }
        })
        .catch(error => {
          this.form.loading = false;

          this.$notify({
            message: error.response.data.error.message,
            type: "error",
            duration: 5000
          });
        });
      }
    },
    checkFields(){
      if( this.form.email == '' || this.form.password == '' ){
        this.$notify.error({
          title: "Todos os campos são obrigatórios",
          message: "Confira se todos os campos foram preenchidos"
        });

        this.form.loading = false;

        return false;
      }else{
        return true;
      }
    }
  }
};
</script>

